import React, { useContext, useEffect, useCallback, useState } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import { Input, Button, Space, Form, notification } from 'antd'
import { UserContext } from '../App'
import Login from '../styles/Login.module.css'
import { EyeInvisibleOutlined, EyeTwoTone, KeyOutlined, LoginOutlined, MailOutlined, UserOutlined } from '@ant-design/icons';
const { Password } = Input

async function login(username, password) {
  return await fetch('http://localhost:3000/auth/login', {
    method: 'post',
    credentials: 'include',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({ username, password })
  })
}

async function register(username, email, password) {
  return await fetch('http://localhost:3000/auth/register', {
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({ username, email, password })
  })

}

function UserCredentialForm({ type }) {

  let navigate = useNavigate()
  let [errors, setErrors] = useState({})
  let [api, contextHolder] = notification.useNotification()
  let { user, setUser } = useContext(UserContext)

  const openNotification = useCallback((message, description) => {
    api.error({ message, description, placement: 'topRight', })
  })

  const onFinish = useCallback(val => {

    let errors = {}

    let {
      username = '',
      email = '',
      password = '',
      passwordConfirm = ''
    } = val

    if (username.length < 5 || val.length > 16)
      errors.username = "Username length can't exceed limit 5 to 16"
    else if (/[\W]/.test(username))
      errors.username = "Username can only contain alphanumeric character and underscore (0-9, A-Z, a-z, _)"

    if (password.length < 8)
      errors.password = "Password must be at least 8 characters"

    if (type === 'register') {
      if (!/^\w+@\w+\.\w+$/.test(email))
        errors.email = "Email doesn't valid"
      if (passwordConfirm !== window.regPsswd)
        errors.passwordConfirm = "Password doesn't match"
    }
    setErrors(errors)
    if (Object.keys(errors).length > 0) return 

    if (type === 'login') {

      login(username, password)
        .then(async res => {

          const body = await res.json()

          switch (res.status) {
            case 200:
              setUser({
                username: body.data.username,
                authStatus: 'success'
              })
              return navigate('/search')
            default:
              setUser({
                username: null,
                authStatus: 'failed'
              })
              return openNotification('failed', body.message)
          }

        })
        .catch(err => {

          console.error(err)
          openNotification('failed', err.message)

        })

    } else if (type === 'register') {
      register(username, email, password)
        .then(async res => {

          const body = await res.json()

          switch (res.status) {
            case 200:
              return navigate('/search')
            default:
              return openNotification('failed', body.message)
          }

        })
        .catch(err => {

          console.error(err)
          openNotification('failed', err.message)

        })
    }

  })

  useEffect(() => {
    if (type !== 'login' || user.authStatus !== 'expired') return
    openNotification('Authorization failed', 'Your session has expired, please log in')
  }, [])

  return (
    <Form
      className={Login.modal}
      onFinish={onFinish}
    >
      {contextHolder}
      <div className={Login.header}>
        <h1 style={{ textTransform: 'capitalize' }}>{type}</h1>
      </div>
      <Space
        size={0}
        direction="vertical"
        className={Login.content}
      >
        <Form.Item
          name='username'
          validateStatus={errors.username && 'error'}
          help={errors.username}
        >
          <Input
            size="large"
            prefix={<UserOutlined />}
            placeholder="username"
            autoComplete='off'
          />
        </Form.Item>
        {
          type === 'register'
          && <Form.Item
            name='email'
            validateStatus={errors.email && 'error'}
            help={errors.email}
          >
            <Input
              size="large"
              prefix={<MailOutlined />}
              placeholder="email"
              autoComplete='off'
            />
          </Form.Item>
        }
        <Form.Item
          name='password'
          validateStatus={errors.password && 'error'}
          help={errors.password}
        >
          <Password
            autoComplete='current-password'
            size="large"
            prefix={<KeyOutlined />}
            placeholder="password"
            onChange={event => window.regPsswd = event.target.value}
            iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
          />
        </Form.Item>
        {
          type === 'register'
          && <Form.Item
            name='passwordConfirm'
            validateStatus={errors.passwordConfirm && 'error'}
            help={errors.passwordConfirm}
          >
            <Password
              autoComplete='current-password'
              size="large"
              prefix={<KeyOutlined />}
              placeholder="password confirmation"
              iconRender={visible => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
            />
          </Form.Item>
        }
        {
          type === 'login' && <Button type="link">Forgot password?</Button>
        }
      </Space>
      <Space className={`flex-center ${Login.footer}`} size={20} direction="horizontal">
        <Button
          className='flex-center'
          type="primary"
          htmlType='submit'
          style={{
            textTransform: 'capitalize',
            height: '100%',
            width: '100%'
          }}
          icon={<LoginOutlined />}
          size="large"
        >
          {type}
        </Button>
        <Button
          size="large"
          style={{ height: '100%', width: '100%' }}
          onClick={() => {
            switch (type) {
              case 'login':
                navigate('/register')
                break
              case 'register':
                navigate('/login')
                break
              default:
                navigate('/')
                break
            }
          }}
        >
          {
            type === 'login'
              ? 'Sign up'
              : (type === 'register' && 'Login')
          }
        </Button>
      </Space>
    </Form>
  )
}

export function LoginElement() {

  let { user } = useContext(UserContext)

  return (
    user?.authStatus === 'success'
      ? <Navigate to='/search' />
      : <UserCredentialForm type='login' />
  )
}

export function RegisterElement() {

  let user = useContext(UserContext)

  return (
    user?.authStatus === 'success'
      ? <Navigate to='/search' />
      : <UserCredentialForm type='register' />
  )
}