import React, {useMemo, useState} from 'react'

export default function({active, onClick, href, label}) {

  const [hover, setHover] = useState(false)
  const onMouseOver = () => setHover(true)
  const onMouseLeave = () => setHover(false)

  const navLinkStyle = {
    textDecoration: 'none',
    fontSize: '17px',
    fontWeight: '500',
    color: active ? 'rgba(255,255,255,.6)': 'rgba(0,0,0,.3)'
  }

  const navBottomlineStyle = {
    position: 'absolute',
    bottom: '0',
    left: '50%',
    transform: 'translateX(-50%)',
    width: (active || hover) ? '80%' : '0',
    height: '3px',
    background: 'rgba(255,255,255,.6)',
    borderRadius: '100px 100px'
  }

  return (
    <div style={{position: 'relative'}}>
      <a style={navLinkStyle} href={href} onClick={onClick} onMouseOver={onMouseOver} onMouseLeave={onMouseLeave} >{label}</a>
      <div style={navBottomlineStyle}/>
    </div>
  )
}