import { LoadingOutlined } from '@ant-design/icons'
import { Spin } from 'antd'
import React from 'react'

const Loading = ({id ,message}) => (
  <div
    id={id}
    style={{
      display: 'none',
      position: 'fixed',
      height: '100%',
      width: '100%',
      background: 'rgba(0,0,0,.2)',
      zIndex: 10
    }}
  >
    <div style={{
      background: 'white',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'space-evenly',
      alignItems: 'center',
      borderRadius: '10px',
      aspectRatio: 4/3,
      width: '30%',
      padding: '30px',
      textAlign: 'center',
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%,-50%)'
    }}>
      <span style={{fontSize: '24px', fontWeight: '500', color: 'var(--fontColorDark)'}}>{message}</span>
      <Spin indicator={<LoadingOutlined style={{ fontSize: 50 }} spin />} />
    </div>
  </div>
)

export default Loading
