import { createBrowserRouter, createRoutesFromElements, Route } from 'react-router-dom';
import Root from './components/Root'
import { LoginElement as Login, RegisterElement as Register } from './components/Login'
import { SearchMovie, SearchFav, SearchWatchList } from './components/Root/Layout/Content/CardContainer'
import './styles/index.css'

const router = createBrowserRouter(
  createRoutesFromElements(
    <>
      <Route path='/register' element={<Register />} />
      <Route path='/login' element={<Login />} />
      <Route path='/' element={<Root />}>
        <Route path='search' element={<SearchMovie />} />
        <Route path='fav' element={<SearchFav />} />
        <Route path='watch-list' element={<SearchWatchList />} />
        <Route path='*' element={<p>404 page not found</p>} />
      </Route>
    </>
  )
)

export default router