import { Button } from "antd"
import React, { useState, useContext } from 'react'
import NavLink from './Nav/NavLink'
import { UserContext } from '../../../../App'

export default function ({ className, active: init }) {
  const [active, setActive] = useState(init)
  let { setUser } = useContext(UserContext)

  return (
    <div className={className} style={{ alignItems: 'center', gap: '30px' }}>
      {['Home', 'About', 'Contact'].map((nav, i) => (
        <NavLink
          key={i}
          active={active === i ? true : false}
          href="/"
          label={nav}
          onClick={e => { e.preventDefault(); setActive(i) }}
        />
      ))}
      <Button
        type="primary"
        size='large'
        style={{ fontWeight: '700', background: 'rgba(0,0,0,.2)' }}
        onClick={async () => {

          await fetch('http://localhost:3000/auth/logout', {
            method: 'delete',
            credentials: 'include'
          })

          setUser({ username: null, authStatus: null })

        }}
      >Log out</Button>
    </div>
  )

}