import React, { useState, useContext, useEffect, useCallback } from 'react';
import { useNavigate, Outlet } from 'react-router-dom';
import { Layout as AntdLayout } from 'antd';
import SideBar from './Root/SideBar';
import Loading from './Root/Loading';
import Layout from './Root/Layout';
import { UserContext } from '../App'
import '../styles/index.css'

export default function Root() {

  let { user } = useContext(UserContext)
  let navigate = useNavigate()

  useEffect(() => {
    if (window.location.pathname === '/') navigate('/search')
  }, [])

  useEffect(() => {
    switch (user?.authStatus) {
      case 'pending':
        setLoadingMessage('Authorizing...')
        showLoading(true)
        break
      case 'success':
        showLoading(false)
        break
      default:
        navigate('/login')
        break
    }
  }, [user])

  let [show, setShow] = useState(false)
  let [select, setSelect] = useState(0)
  let [loadingMessage, setLoadingMessage] = useState()

  const showLoading = useCallback((bool) => {
    document.body.style.setProperty('overflow', bool ? 'hidden' : 'initial')
    document.getElementById('loading').style.setProperty('display', bool ? 'flex' : 'none')
  })

  return (
    <AntdLayout style={{
      minHeight: `max(100vh, ${window.innerHeight / window.innerWidth * 500}px)`,
      width: 'max(100%, 500px)'
    }}>
      <Loading id='loading' message={loadingMessage} />
      <SideBar select={select} show={show} setShow={setShow} />
      <Layout setShow={setShow}>
        <Outlet context={{ showLoading, setSelect, setLoadingMessage }} />
      </Layout>
    </AntdLayout>
  )
}