import { Layout } from 'antd';
import { default as Header } from './Layout/Header'
import { default as Content } from './Layout/Content'
import { default as Footer } from './Layout/Footer'

export default function ({ setShow, children }) {

  return (
    <Layout>
      <Header setShow={setShow} />
      <Content>
        {children}
      </Content>
      <Footer />
    </Layout>
  )
}

export { Header, Content, Footer }