import React, { useState, useEffect } from 'react'
import { ConfigProvider } from 'antd'
import { RouterProvider } from 'react-router-dom'
import router from './router'
import { defaultTheme } from './Themes'
import getCookie from './hooks/getCookie'
import './styles/index.css'

const Theme = defaultTheme
const UserContext = React.createContext()

document.documentElement.style.setProperty('--vw', window.innerWidth)
document.documentElement.style.setProperty('--vh', window.innerHeight)

for (let key in Theme.token) {
  document.documentElement.style.setProperty(`--${key}`, Theme.token[key])
}

function App() {

  let [user, setUser] = useState({username: null, authStatus: 'pending'})

  useEffect(() => {

    const cookie = getCookie('sid')

    if (cookie === '') return setUser({username: null, authStatus: null})

    fetch('http://localhost:3000/auth', {
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${cookie}`
      }
    }).then(async res => {
  
      if (res.status === 200) {
        const body = await res.json()
        return setUser({ username: body.username, authStatus: 'success' })
      }

      if (res.headers.get('X-Session-Expired') === 'true')
        return setUser({ username: null, authStatus: 'expired' })
  
      if (res.status === 500) console.log(await res.text())
  
      return setUser({ username: null, authStatus: 'failed' })

    }).catch(err => {

      console.error(err)
      return setUser({ username: null, authStatus: 'failed' })

    })

  }, [])

  return (
    <ConfigProvider theme={Theme}>
      <UserContext.Provider value={{user, setUser}}>
        <RouterProvider router={router} />
      </UserContext.Provider>
    </ConfigProvider>
  )
}

export {UserContext}
export default App;
