import React, { useEffect, useMemo, useState } from 'react'
import { useOutletContext, useSearchParams } from 'react-router-dom';
import { HeartOutlined, PlaySquareOutlined } from '@ant-design/icons';
import { Empty, notification } from "antd"
import Card from './CardContainer/Card'
import getCookie from '../../../../hooks/getCookie'

export default function CardContainer({ init = [], setInit, movies, setMovies, actions }) {

  let [api, contextHolder] = notification.useNotification()

  return (
    <div
      style={{
        display: 'flex',
        flexWrap: 'wrap',
        gap: '10px',
        justifyContent: 'space-between',
        alignItems: 'center',
        flexGrow: 1,
        padding: '54px 0',
      }}
    >
      {
        movies.length === 0
          ? <Empty />
          : movies.map(({ id, image, title, description }) => {
            return (
              <Card
                api={api}
                actions={actions}
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  width: '200px',
                  height: '500px',
                  whiteSpace: 'unset'
                }}
                bodyStyle={{ display: 'flex' }}
                key={id}
                className='shadow-sm'
                cover={
                  <img
                    alt={`${title} poster`}
                    src={image}
                  />
                }
                meta={{ id, image, title, description }}
                init={[...init]}
                setInit={setInit}
                movies={[...movies]}
                setMovies={setMovies}
              />
            )
          })
      }
      {contextHolder}
    </div>
  )
}

export function SearchMovie() {

  let { showLoading, setSelect, setLoadingMessage } = useOutletContext()
  let [searchParams] = useSearchParams()
  let [movies, setMovies] = useState([])

  useEffect(() => setSelect(0), [])
  useEffect(() => {
    let title = searchParams.get('title')
    if (!title || title === '') return setMovies([])
    fetch(`https://imdb-api.com/en/API/SearchMovie/k_m2g8fxun/${title}`)
      .then(res => res.json())
      .then(res => {
        setMovies(res.results)
        showLoading(false)
      })
    showLoading(true)
    setLoadingMessage('Searching for movies, please wait...')
  }, [searchParams])

  return (
    <CardContainer
      actions={[
        {
          type: 'add',
          tooltip: 'Add to favorite',
          Element: HeartOutlined,
          path: 'fav'
        },
        {
          type: 'add',
          tooltip: 'Add to watch list',
          Element: PlaySquareOutlined,
          path: 'watchList'
        },
      ]}
      movies={movies}
      setMovies={setMovies}
    />
  )
}






export function SearchFav() {

  const cookie = getCookie('sid')

  let [movies, setMovies] = useState([])
  let [init, setInit] = useState([])
  let { showLoading, setSelect, setLoadingMessage } = useOutletContext()
  let [searchParams] = useSearchParams()
  let title = searchParams.get('title') ?? ''

  useEffect(() => {

    setSelect(1);
    fetch('http://localhost:3000/fav', {
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${cookie}`
      }
    }).then(async res => {
      if (res.status !== 200) return setInit([])
      setInit((await res.json()).data)
    })

  }, [])

  useEffect(() => {

    setLoadingMessage('Searching for movies, please wait...')
    showLoading(true)
    setMovies(init.filter(e => new RegExp(title, 'i').test(e.title)))
    showLoading(false)

  }, [title, init])

  return <CardContainer
    actions={[
      {
        type: 'delete',
        tooltip: 'Remove from favorite',
        Element: HeartOutlined,
        path: 'fav'
      }
    ]}
    init={init}
    setInit={setInit}
    movies={movies}
    setMovies={setMovies}
  />
}







