import React from 'react'
import {theme} from 'antd'
import { NavLink } from 'react-router-dom'

export default function({ active, href, label}) {

  const {token: {sideBarFontColor}} = theme.useToken()

  return (
    <div
      style={{
        display: 'flex',
        gap: '25px',
        alignItems: 'center',
        height: '50px',
        borderRadius: '0 7px 7px 0',
        background: active ? 'rgba(0,0,0,.2)' : 'none'
      }}
    >
      <div
        style={{
          display: active ? 'initial' : 'none',
          height: '100%',
          width: '3px',
          background: sideBarFontColor
        }}
      />
      <NavLink
        to={href}
        style={{
          textDecoration: 'none',
          fontSize: '23px',
          position: 'relative',
          left: active ? 'initial' : '45px',
          fontWeight: `${500 + active*200}`,
          background: 'none',
          color: active ? sideBarFontColor : 'rgba(0,0,0,.3)'
        }}
      >
        {label}
      </NavLink>
    </div>
  )

}