import { Card, Tooltip } from 'antd'
import { useCallback, useEffect } from 'react';
import getCookie from '../../../../../hooks/getCookie'
import '../../../../../styles/index.css'

const { Meta } = Card;


async function getMovie(path) {

  const cookie = getCookie('sid')

  return await fetch(`http://localhost:3000/${path}`, {
    credentials: 'include',
    headers: {
      'Content-Type': 'application/json',
      'Authorization': `Bearer ${cookie}`
    }
  })
}

async function uploadMovie(path, { id, image, title, description }) {

  const cookie = getCookie('sid')

  return await fetch(`http://localhost:3000/${path}`, {
    method: 'post',
    headers: {
      Authorization: `Bearer ${cookie}`,
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      movies: [
        { id, image, title, description }
      ]
    })
  })

}

async function deleteMovie(path, id) {

  const cookie = getCookie('sid')

  return await fetch(`http://localhost:3000/${path}`, {
    method: 'delete',
    headers: {
      Authorization: `Bearer ${cookie}`,
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      movies: [ id ]
    })
  })

}






export default function ({
  api,
  actions,
  className,
  style,
  cover,
  meta: { id, image, title, description },
  init,
  setInit,
}) {

  let openNotification = useCallback((ok, message, description) => {
    if (ok) api.success({ message, description, placement: 'topRight', })
    else api.error({ message, description, placement: 'topRight', })
  })

  return (
    <Card
      headStyle={{width: '100%'}}
      hoverable
      className={className}
      style={style}
      cover={cover}
      title={title}
      actions={
        actions.map(({type, tooltip, Element, path}) => (
          <Tooltip
            placement="bottom"
            title={tooltip}
          >
            <Element
              onClick={
                type === 'add'
                ? () => {
                  uploadMovie(path, { id, image, title, description })
                  .then(async res => {
                
                    let message;
                
                    if (/application\/json/.test(res.headers.get('Content-Type'))) {
                      message = (await res.json()).message
                    } else {
                      message = await res.text()
                    }
                
                    if (res.status >= 400 && res.status <= 599) return openNotification(false, 'Failed', message);
                
                    openNotification(true, 'Success', message)
                
                  })
                  .catch(err => {
                
                    console.error(err)
                    openNotification(false, 'Error Occured', err.message)
                
                  })
                } : type === 'delete'
                ? () => {
                  deleteMovie(path, id)
                  .then(async res => {
                
                    let message;
                
                    if (/application\/json/.test(res.headers.get('Content-Type'))) {
                      message = (await res.json()).message
                    } else {
                      message = await res.text()
                    }
                
                    if (res.status >= 400 && res.status <= 599) return openNotification(false, 'Failed', message);

                    const i = init.findIndex(movie => movie.id === id)

                    if (i === -1)
                      return openNotification(false, 'Failed', `"${title}" already removed from ${path === 'fav' ? 'Favorite' : 'Watch List'}`)

                    init.splice(i, 1)
                    setInit(init)
                    // setMovies(movies)
                    openNotification(true, 'Success', `Removed "${title}" from ${path === 'fav' ? 'Favorite' : 'Watch List'}`)
                
                  })
                  .catch(err => {
                
                    console.error(err)
                    openNotification(false, 'Error Occured', err.message)
                
                  })
                } : () => console.log({ id, image, title, description })
              }
            />
          </Tooltip>
        ))
      }
    >
      <Meta description={description} />
    </Card>
  )
}
