import React from 'react'
import { Offcanvas } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { HomeOutlined } from '@ant-design/icons'
import { theme } from 'antd'
import SideBarLink from './SideBar/SideBarLink'
import SiderStyle from '../../styles/Sidebar.module.css'

const siderNav = (...labels) => (
  labels.map((label, key) => (
    <Link
      key={key}
      style={{
        display: 'flex',
        textDecoration: 'none',
        alignItems: 'center',
        height: '50px',
      }}
    >
      <span
        to='google.com'
        style={{
          padding: '0 20px',
          fontSize: '23px',
          fontWeight: 500,
          background: 'none',
          color: 'rgba(0,0,0,.3)'
        }}
      >
        {label}
      </span>
    </Link>
  ))
)

export default function ({ select, show, setShow }) {

  const { token: { sideBarColorBg } } = theme.useToken()
  const handleClose = () => setShow(false)

  return (
    <Offcanvas show={show}
      onHide={handleClose}
      backdrop="static"
      style={{
        background: sideBarColorBg,
        width: '300px'
      }}
    >
      <Offcanvas.Header closeButton />
      <Offcanvas.Body>
        <div
          className={SiderStyle.nav}
          style={{
            flexDirection: 'column',
            margin: '20px',
            borderRadius: '13px',
            background: 'rgba(255,255,255,.05)'
          }}
        >
          {siderNav('Home', 'About', 'Contact')}
        </div>
        {
          [
            { label: 'Search', href: 'search' },
            { label: 'Favorite', href: 'fav' },
            { label: 'Watch List', href: 'watch-list' }
          ].map(({label, href}, key) => (
            <SideBarLink
              key={key}
              label={label}
              href={href}
              active={select ===  key}
            />
          ))
        }
      </Offcanvas.Body>
    </Offcanvas>
  )
}